<template>
  <div class="navigation">
    <!--  -->
    <v-treeview
      rounded
      hoverable
      selectable
      transition
      activatable
      item-key="id"
      open-on-click
      return-object
      multiple-active
      v-model="marker"
      item-text="name"
      :items="fixation"
      :open.sync="open"
      :active.sync="marker"
      :load-children="load"
      @update:active="active"
    />
    <!--  -->
    <v-dialog
      persistent
      width="300"
      v-model="loading"
      overlay-color="#fff"
      overlay-opacity=".17"
    >
      <v-card color="primary" dark>
        <v-card-text class="pt-2">
          Please stand by
          <v-progress-linear indeterminate color="white" class="mb-0" />
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "navigation",
  data: () => ({
    open: [],
    marker: [],
    fixation: [],
    loading: false,
  }),
  watch: {
    marker: {
      handler(value) {
        this.saveMarker([...value]);
      },
      immediate: true,
      deep: true,
    },
  },
  mounted() {
    // 
    this.myFixation();
  },
  methods: {
    // 
    ...mapActions(["saveMarker"]),
    // 
    myFixation() {
      this.$axios.post("/device-center/SpaceManage/list", {}).then((res) => {
        if (res) {
          // 
          for (const i of res.data) {
            i.children = i.subSpaceManage;
            for (const n of i.subSpaceManage) {
              n.children = [];
            }
          }
          // 
          this.fixation = res.data;
        }
      });
    },
    // ID
    myId(capsule, item) {
      this.$axios
        .get("/api-device/device/queryDeviceIds", {
          params: {
            capsule,
          },
        })
        .then((res) => {
          // 
          this.myDevice(res.data, item);
        });
    },
    // ids
    myDevice(ids, item) {
      this.$axios
        .post("/dataManage/deviceData/realTimeData", {
          ids,
        })
        .then((res) => {
          if (res) {
            // 
            item.children = [...res.result];
            // 
            this.open.push({ ...item });
            // 
            this.loading = false;
          }
        });
    },
    // 
    load(item) {
      // 
      this.loading = true;
      // 
      this.myId(item.id, item);
    },
    // 
    active(value) {
      this.marker = [...value];
    },
  },
};
</script>
